<template>
  <div>
    <loading :active.sync="isLoading">
      <Circle4/>
    </loading>

    <!-- Navbar start -->
    <Navbar @changeLanguage="setLang"/>
    <!-- Navbar end -->

    <!-- Banner start -->
    <div class="contact-banner bg-setting banner-img-filter"></div>
    <!-- Banner end -->

    <!-- Content start -->
    <section class="section">
      <div class="container">
        <div class="row">
          <h3 class="col-md-8 offset-md-2 section-title">{{ $t('ContactTitle')}}</h3>
        </div>
        <div class="row">
          <ValidationObserver ref="form" class="contact-content col-md-8 offset-md-2 mb-3">
            <div class="form-row">
              <ValidationProvider
                v-slot="{ failed, errors }"
                name="name"
                rules="required"
                class="col-md-6 mb-3">
                <label for="userName">{{ $t('ContactFormName')}}</label>
                <input
                  type="text"
                  class="form-control"
                  :class="{ 'is-invalid': failed }"
                  id="userName"
                  :placeholder="[[$t('ContactFormNameHolder')]]"
                  v-model="name">
                <span v-if="failed" class="text-danger">{{ errors[0] }}</span>
              </ValidationProvider>
              <div class="col-md-6 mb-3">
                <label for="userPhone">{{ $t('ContactFormPhone')}}</label>
                <input type="text" class="form-control" id="userPhone" :placeholder="[[$t('ContactFormPhoneHolder')]]" v-model="phone">
              </div>
            </div>
            <div class="form-group">
              <ValidationProvider
                v-slot="{ failed, errors }"
                name="email"
                rules="required"
                class="mb-3">
                <label for="userEmail">{{ $t('ContactFormEmail')}}</label>
                <input
                  type="text"
                  class="form-control"
                  :class="{ 'is-invalid': failed }"
                  id="userEmail"
                  :placeholder="[[$t('ContactFormEmailHolder')]]"
                  v-model="email">
                <span v-if="failed" class="text-danger">{{ errors[0] }}</span>
              </ValidationProvider>
            </div>
            <div class="form-row">
              <div class="col-md-6 mb-3">
                <div for="userSalutation">{{ $t('ContactFormSalutation')}}</div>
                <div class="radio-mt-space">
                  <div class="form-check form-check-inline">
                    <input class="form-check-input" type="radio" id="male" value="male" v-model="salutation" name="salutation">
                    <label class="form-check-label" for="male">{{ $t('ContactFormSalutationMale')}}</label>
                  </div>
                  <div class="form-check form-check-inline">
                    <input class="form-check-input" name="salutation" type="radio" id="female" value="female" v-model="salutation">
                    <label class="form-check-label" for="female">{{ $t('ContactFormSalutationFemale')}}</label>
                  </div>
                </div>
              </div>
              <div class="col-md-6 mb-3">
                <label for="userCompanyName">{{ $t('ContactFormCompanyName')}}</label>
                <input
                  type="text"
                  name="companyName"
                  class="form-control"
                  id="userCompanyName"
                  :placeholder="[[$t('ContactFormCompanyNameHolder')]]"
                  v-model="companyName">
              </div>
            </div>
            <div class="form-group">
              <label for="userInfo">{{ $t('ContactFormInfo')}}</label>
              <textarea class="form-control contact-text-area" name="contact" id="userInfo" v-model="contact"></textarea>
            </div>
            
          </ValidationObserver>
        </div>
        <div class="row">
          <div class="col-md-4 offset-md-4">
            <button class="btn btn-primary btn-block" @click="onSubmit">{{ $t('ContactFormSubmit')}}</button>
          </div>
        </div>
      </div>
    </section>
    <!-- Content end -->

    <!-- Footer start -->
    <Footer :isChangeLanguge = "changeLanguge"/>
    <!-- Footer end -->
  </div>
</template>
<script>
import Navbar from '../components/Navbar'
import Footer from '../components/Footer'
import { Circle4 } from 'vue-loading-spinner'
import emailjs from 'emailjs-com';

export default {
  name: 'Contact',
  components:{
    Navbar,
    Footer,
    Circle4
  },
  data(){
    return{
      name: '',
      phone: '',
      email: '',
      salutation:'',
      companyName: '',
      contact: '',
      isLoading: false,
      changeLanguge: true
    }
  },
  methods:{
    onSubmit () {
      this.$refs.form.validate().then((success) => {
        if (success) {
          console.log('Success')
        } else {
          console.log('Falut')
          return
        }
      });

      const templateParams = {
        name: this.name,
        phone: this.phone,
        email: this.email,
        salutation:this.salutation,
        companyName: this.companyName,
        contact: this.contact
      };
      emailjs.send(process.env.VUE_APP_SERVICE_ID, process.env.VUE_APP_TEMPLATE_ID, templateParams, process.env.VUE_APP_USER_ID)
        .then((response) => {
          console.log('SUCCESS!', response.status, response.text);
        }, (err) => {
          console.log('FAILED...', err);
        });
      
    },
    // 儲存切換的語系
    setLang (value) {
      this.$store.commit('setLang', value);
      this.$i18n.locale = value;
      localStorage.setItem('footmark-lang', value);
      this.changeLanguge = !this.changeLanguge
    }
  },
  created(){
    this.isLoading = true
  },
  mounted(){
    this.isLoading = false
  }
}
</script>
<style lang="scss">
@import '../assets/scss/all';

.contact-banner {
  width: 100%;
  height: 400px;
  background-image: url('../assets/images/contactBanner.jpg');
}

.form-control {
  padding: 10px;
}

textarea.form-control.contact-text-area{
  height: 250px;
  resize: none;
}

.radio-mt-space{
  margin-top: 15px;
}
</style>